import Modal from 'react-modal'
import { Formik, Field, Form } from 'formik';
import "./modal.css"
import { addTransactions } from 'components/services/api/api';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  }
};

Modal.setAppElement('#modal');

const ModalWindow = ({modalIsOpen, closeModal, isUpdate}) => {
//   let subtitle;
  

//   function afterOpenModal() {
//     subtitle.style.color = '#f00';
//   }

  return (
    <div id='container'>
      <Modal
        isOpen={modalIsOpen}
        // onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <button className='closeBtn' onClick={closeModal}>X</button>
        <div>
            <Formik
            initialValues={{
                typeProduct: '',
                product: '',
                quantity: '',
                name: '',
                telNumber: '',
                town: '',
                post: '',
                price: '',
                payment: '',
                cost: ''
            }}
            onSubmit={(values) => {
                addTransactions(values)

                closeModal()
            }}
            >
            <Form>
                <label htmlFor="typeProduct"> </label>
                <Field className='input' id="typeProduct" name="typeProduct" placeholder="" />

                <label htmlFor="product"> </label>
                <Field className='input' id="product" name="product" placeholder="  + " />

                <label htmlFor="quantity"></label>
                <Field className='input' id="quantity" name="quantity" placeholder="1"/>
                
                <label htmlFor="name"> </label>
                <Field className='input' id="name" name="name" placeholder=" "/>

                <label htmlFor="telNumber"> </label>
                <Field className='input' id="telNumber" name="telNumber" placeholder="0631234563"/>

                <label htmlFor="town"> </label>
                <Field className='input' id="town" name="town" placeholder=""/>

                <label htmlFor="post"> </label>
                <Field className='input' id="post" name="post" placeholder=" 24"/>

                <label htmlFor="price"></label>
                <Field className='input' id="price" name="price" placeholder="250"/>
                
                <label htmlFor="payment"></label>
                <Field className='input' id="payment" name="payment" placeholder="250"/>

                <label htmlFor="cost"></label>
                <Field className='input' id="cost" name="cost" placeholder="120"/>

                {isUpdate ? <button type='button'></button> : <button  type="submit"></button>}
            </Form>
            </Formik>
                </div>
            </Modal>
        </div>
  );
}

export default ModalWindow

