// import transactions from "../../transactions.json"
import "./table.css"
import { removeTransactions } from "components/services/api/api"
import { AiFillEdit, AiFillDelete } from "react-icons/ai";

const Table = ({transactions, isUpDateTransaction, openModal}) => {
    
    const onDelBtnClick = (id) => {
        console.log(id)
        removeTransactions(id)
    }

    const onUpdBtnClick = () => {
        isUpDateTransaction()
        openModal()
    }


    return (
        <table className="table table-hover table-dark">
            <thead>
                <tr>
                    <th scope="col"> </th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                    <th scope="col"> </th>
                    <th scope="col"> </th>
                    <th scope="col"> </th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col">  %</th>
                </tr>
            </thead>
            <tbody>
                {transactions.map(el => (
                    <tr key={el._id}>
                        <td>{el.typeProduct}</td>
                        <td>{el.product}</td>
                        <td>{el.quantity}</td>
                        <td>{el.name}</td>
                        <td>{el.telNumber}</td>
                        <td>{el.town}</td>
                        <td>{el.post}</td>
                        <td>{el.price}</td>
                        <td>{el.total}</td>
                        <td>{el.payment}</td>
                        <td>{el.balanceToBePaid}</td>
                        <td>{el.cost}</td>
                        <td>{el.earnings}</td>
                        <td>{parseFloat(el.percentEarning.toFixed(2))}</td>
                        <td>
                            <AiFillDelete className="btn-table btb-del" onClick={() => onDelBtnClick(el._id)}/>
                        </td>
                        <td>
                            <AiFillEdit className="btn-table btn-upd" onClick={() => onUpdBtnClick()}/>
                        </td>
                   </tr>
                ))}
                
            </tbody>

        </table>
    )
}

export default Table